nav{
    //background: blue;
    position: absolute;
    width: 100%;
    left: 0;
    top: 0;
    z-index: 10;
    padding: 0 56px;
    padding-top: 48px;
    #nav-logo{
        width: 49px;
        height: 55px;
        display: block;
        img{
            width: 100%;
        }
    }
    .nav-links{
        position: absolute;
        bottom: 0;
        right: 200px;
        a{
            font-size: 24px;
            letter-spacing: 1px;
            font-style: italic;
            color: white;
            text-decoration: none;
            &:hover{
                text-decoration: underline;
            }
            margin-left: 126px;
        }
    }
}